@charset 'utf-8';
@import '../base/global.scss';
/* 
 * content.scss
 * 内容提供
*/

// content图片路径
$current-img-path: '../' + $images-path;


.tabSwitch2 {
  cursor: pointer;
}

/* 查看合计 */
.viewCount {
  min-width: 6.333rem;
  font-family: "PingFang SC", "Microsoft yahei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  padding: 0.4rem 0.666rem;
  position: relative;
  box-sizing: border-box;
}

.viewCount .label,
.viewCount .value {
  float: left;
  word-wrap: break-word;
  text-align: left;
}

.viewCount .label {
  text-align: center;
  margin: 0.13rem 0;
  width: 1.8rem;
}
.viewCount .value {
  margin: 0.13rem 0;
}

/* 提示 */
.tips h1 {
  font-size: 1.4em;
  margin-bottom: 0.5em;
  color: $Theme-text-primary;
}

/* 入网产品 */
.productItem {
  padding: 0 0.5em;
  .title-h2 {
    border-left: 3px solid #009688;
  }
}

/* 网点管理器 */
.shopMannage {
  .title {
    font-size: 1.7em;
  }
  .addShop {
    color: #03a9f4;
    font-size: 1.5em;
    vertical-align: middle;
    cursor: pointer;
  }
  .shopItem .item  {
    float: left;
    margin-right: 0.5em;
  }
}


/* 首页 */
.h-315 {
  height: px2rem(315px);
}
.h-320 {
  height: px2rem(320px);
}

.area-block {
  height: 100%;
  background: white;

  .title {
    @extend %fix;
    color: $Theme-text-primary;
    padding: px2rem(5px) px2rem(20px);
    border-bottom: 1px solid #eae7e7;
    line-height: 2em;

    .primary {
      color: $Theme-text-primary;
      font-size: 1.4em;
    }

  }

  .plane {
    -webkit-overflow-scrolling: touch;
    color: $Theme-text-secondary;
  }

  .line {
    @extend %ib;
    width: px2rem(1px);
    height: 1em;
    margin: 0 px2rem(10px);
    background: #9E9E9E;
  }


}
.filter-item {
  font-size: 0.9em;
  display: inline-block;
  color: $Theme-text-primary;
  vertical-align: middle;
  &.active {
    color: $Theme-color;
  }
}


.color-green {
  color: #1abc9c;
}
.color-blue {
  color: #297cbb;
}
.color-red {
  color: #F44336;
}
.color-orange {
  color: #f39c12;
}
.bg-green {
  background-color: #1abc9c;
}
.bg-orange {
  background-color: #f39c12;
}
.bg-blue {
  background-color: #297cbb;
}
.bg-red {
  background-color: #E91E63;
}
.bg-yellow  {
  background-color: #f1c40f;
}

.tag {
  padding: 0 0.6em;
  border-radius: 0.6em;
  color: #fff;
}


.welcome {
  background: #f5f5f5;
  max-width: 100vw;

  // 个人信息
  .decs {
    padding: 1.5em 0.4em 0.4em;

    // 头像
    .avatar {
      width: px2rem(65px);
      float: left;
      // display: inline-block;
      vertical-align: text-bottom;
    }
    .avatar-img {
      width: px2rem(65px);
      height: px2rem(65px);
      background: url($current-img-path + 'backindex1.png') no-repeat;
      background-position: -(px2rem(3px)) 0;
      background-size: px2rem(70px) px2rem(110px);
    }

    // 详细信息
    .detailed {
      overflow: hidden;
      // display: inline-block;
      vertical-align: top;
      margin-top: 0.2em;
      line-height: 1.7;
    }

    .company-name {
      color: $Theme-text-primary;
    }

    .line {
      @extend %ib;
      width: px2rem(1px);
      height: 1em;
      margin: 0 px2rem(10px);
      background: #9E9E9E;
    }

    .authenticate {
      line-height: 1.2;
      .ico {
        @extend %ib;
        font-size: 1.5em;
        color: $Theme-color;
      }
    }

    .amount-count {
      line-height: 1.9em;
      color: $Theme-text-third;
      .depict {
        @extend %ib;
        width: px2rem(100px);
        color: $Theme-text-secondary;
      }
      .amount {
        @extend %ib;
        margin-left: 1em;
      }

    }



  }

  // 今日数据统计
  .statistics {
    font-size: 1.1em;
    padding: 0.6em;

    li {
      margin-bottom: 0.4em;
    }

    .ico {
      display: inline-block;
      vertical-align: text-bottom;
      width: 0.6em;
      height: 0.6em;
      border-radius: 50%;
      margin: 0 0.4em;
    }

    .content {
      display: inline-block;
      vertical-align: text-top;
      line-height: 2;
    }

    .depict {
      color: $Theme-text-primary;
    }
    .count span{
      margin-right: 0.2em;
    }


  }

  // 公告列表
  .bulletin {
    iframe {
      width: 100%;
      height: px2rem(275px);
    }
  }

  // 图表
  .chart {
    padding: 1em 1em 0;
    height: px2rem(280px);
  }

  @media screen and (max-width: 1280px) {
    .col-1.w-p-70,
    .col-2.w-p-30 {
      width: 100%;
    }

    .information.w-p-65 {
      width: 100%;
      height: px2rem(500px);
      margin-bottom: 0.8em;
    }
    .today-data.w-p-35 {
      width: 100%;
      margin-bottom: 0.8em;
    }

    #pay-and-bay-chart,
    #analysis-chart {
      margin-bottom: 0.8em;
    }

    .bulletin {
      iframe {
        height: px2rem(375px);
      }
    }


    .h-315 {
      height: px2rem(430px);
    }
    .h-320 {
      height: px2rem(430px);
    }

    .area-block.mr-10,
    .h-320.mr-10 {
      margin-right: 0;
    }

    .decs {
      .amount-count {
        width: px2rem(900px);
        .depict {
          width: px2rem(130px);
        }
      }
    }




  }






}

/* 新闻列表 */
.bulletinList {
  .circle {
    background: #e0dddd;
    display: inline-block;
    position: absolute;
    left: 0px;
    top: px2rem(15px);
    width: px2rem(7px);
    height: px2rem(7px);
    border-radius: px2rem(7px);
  }

  .list {
    margin: px2rem(10px) px2rem(20px) px2rem(5px) px2rem(20px);
    padding: 0;
    line-height: initial;
  }

  li {
    position: relative;
    padding: px2rem(10px) 0 px2rem(10px) px2rem(15px);
    width: 100%;
    a {
      cursor: pointer;
      display: block;
      overflow: hidden;
      @extend %ellipsis;
    }

    .text {
      @extend %ib;
      @extend %ellipsis;
      width: 75%;
    }
    .plus {
      @extend %ib;
      width: 22%;
    }

  }

}

/* 权限管理 */
.role-group {
  padding: 0.8em;
  padding-top: 0.8em;
  font-size: 1.1em;
}
.role-group .row {
  padding-bottom: 2em;
  padding-top: 1.2em;
  border-bottom: 1px dashed #ddd;
}
.role-group .row:first-child {
  padding-top: 0;
}
.role-group .row:last-child {
  border-bottom: none;
}
.primary > .check-list,
.secondary-row > .check-list {
  float: left;
  width: 2.9rem;
}
.sub {
  position: relative;
  overflow: hidden;
}
.featur {
  float: left;
  width: 3rem;
  overflow: hidden;
  margin: 0 0.8em;
}


/* 商户详情 */
.detail {
  @extend %fix;
  color: $Theme-text-third;
  line-height: 2.5;

  .col {
    float: left;
  }

  .row {
    @extend %fix;
  }

  .label {
    width: px2rem(100px);
    float: left;
    text-align: right;
    margin-right: px2rem(20px);
    color: $Theme-text-primary;
    word-break: break-word;
  }
  .value {
    float: left;
    width: calc(100% - 2.23rem);
  }


}

/* 证件信息 */
.img-box {
  padding: 0.8em;
  img {
    max-width: 100%;
    box-shadow: 0 0 5px rgba(0,0,0,0.3);
    cursor: pointer;
  }
}
.desc {
  text-align: center;
  color: $Theme-text-secondary;
  margin-bottom: 0.5em;
}
.look-loading-img {
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
  filter: gray;
}

/* 修改证件信息图片 */
.id-phone {

  li {
    line-height: 2.5;
  }


  .ico {
    vertical-align: sub;
    // visibility: hidden;
    font-size: 1.5em;
    margin: 0 0.2em;
    color: #297cbb;
    .fa:before {
      content: "\f15b";
    }
  }

  li.modify .ico {
    color: rgb(255, 153, 0);
    visibility: visible;
    .fa:before {
      content: "\f06a";
    }
  }



}



.productType {
  margin-bottom: 0.8em;
}

/* 列表 */
.list {
  padding: 0.8em;
  line-height: 2.5;

  li {
    @extend %fix;
  }

  .info-title {
    float: left;
    width: px2rem(100px);
    text-align: right;
    color: $Theme-text-primary;
    margin-right: px2rem(20px);
  }
  .info-value {
    float: left;
    @extend %ellipsis;
    width: calc(100% - 2.23rem);
    color: $Theme-text-third;
  }
  // 列表值换行
  .info-value.break-word {
    word-wrap: break-word;
    white-space: initial;
  }
}
